.PageNotFound{
    background: url('../../../images/Fondo-body.png');
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    text-align: center;
    color: #fff;
    height: 80vh;
    text-shadow: 0px 3px 6px #000;
}
.title{
    font-size: 3rem;
    width: 100%;
    margin: 0;
    text-align: center;
    font-family: century gothic, sans-serif;
}
.output{
    font-size: 13rem;
    font-weight: 800;
    width: 100%;
    line-height: 0;
    font-family: 'Josefin Sans', century gothic, sans-serif;
}
.n404__icon{
    height: 200px;
    width: 200px;
    filter: drop-shadow(0 0 3px #000);
}
