.result {
    width: 95%;
    max-height: 300px;
    overflow-y: auto;
    background: url('../images/Fondo-resultados.png') center;
    background-size: contain;
    background-repeat: repeat-y;

    border-radius: 1rem;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-template-rows: repeat(3, 6rem);
    grid-auto-rows: 6rem;
    row-gap: .5rem;
    justify-items: center;
    padding-top: 1rem;
    box-sizing: border-box;
}

.result__item {
    width: 95%;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.219);
    border-radius: .5rem;
    border: none;
    background-color: #fff;
    font-size: 1rem;
    text-decoration: none;
    text-align: center;
    color: black;
    line-height: 0;
    transition: 200ms;
    
    display: grid;
    justify-items: space-around;
    align-items: center;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 3rem 3rem;
}
.result__item:hover{
    outline: 3px solid #bbb;
    transform: scale(1.02);
}
.operator{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.select{
    border: none;
    font-weight: 500;
    background-color: #adff2f;
    border-radius: 3rem;
    width: fit-content;
    justify-self: center;
    cursor: pointer;
}
.selected{
    background-color: #fdf7ea;
    border: 1px solid #ff8800;
}
.select:hover{
    background-color: #adf5cd;
}