/*ESTILO HEADER---------------------------------------*/
.header {
    display: flex;
    align-items: center;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    background: url('../images/Fondo-de-encabezado.jpg');
    background-size: cover;
    background-position: center;
    color: #fff;
    height: 15rem;
}

.header::after {
    content: "";
    position: absolute;
    background: linear-gradient(transparent, var(--colorAzulBase));
    width: 100%;
    height: 15rem;
    z-index: 1;
}

.header__title {
    flex-grow: 4;
    width: 100px;
    max-height: 10rem;
    display: flex;
    text-shadow: 2px 2px 3px #000;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 3.5rem;
    align-items: center;
    margin-left: 1rem;
    z-index: 2;
}

.header__subtitle-sena {
    font-size: 3rem;
    text-shadow: 2px 2px 3px #000;
    text-decoration: overline;
    text-align: center;
    align-self: flex-end;
    z-index: 2;
}

/*ICONOS - LOGOS*/
.header__icon-sena {
    flex-grow: 0;
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 0;
    margin: 0;
    max-width: fit-content;
    z-index: 2;
}

.header__sena-item {
    display: flex;
    justify-content: center;
    line-height: 0;
    width: 100%;
}

.header__icon {
    width: 10rem;
    height: 10rem;
    position: sticky;
    top: -126px;
    margin-right: 1rem;
    filter: drop-shadow(0px 0px 2px #000000a1);
    transition: 300ms;
    z-index: 2;
}

.header__icon-sena {
    width: 22%;
    height: 22%;
    filter: drop-shadow(0px 0px 5px #000);

}

/*ESTILO NAV---------------------------------------*/
.navegation {
    background-color: var(--colorAzulBase);
    padding: 0;
    height: 4rem;
    position: sticky;
    top: 0;
    z-index: 9;
    border-bottom: 3px solid #202c52;
    z-index: 1;

}

.navegation__options {
    display: flex;
    justify-content: space-between;
    height: 100%;
    font-size: 1.3rem;
    font-family: 'Josefin Sans', sans-serif;
    list-style: none;
    align-items: center;
    margin: 0;
}

.navegation__anchor {
    margin: 0;
    text-decoration: none;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    /* padding: 1rem 3rem 1rem 3rem; */
    width: 100%;
    height: 100%;
    color: #fff;
    border-radius: .8rem;
}

.navegation__item {
    height: 92%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    transition: 90ms;
}

/* .navegation__item:hover{
    background-color: #9498d8;
} */
.navegation__anchor:hover {
    background-color: #9498d8;
}

.active {/*clase para vitas seleccionada*/
    font-weight: 600;
}

.active::before,
.active::after {
    content: "";
    position: relative;
    height: 1rem;
    width: 1rem;
    background: radial-gradient(#fff, #9498d8, var(--colorAzulBase));
    border-radius: 50%;
    margin: 0 .2rem 0 .2rem;
}

.scrol1 {
    animation-timing-function: ease-in-out;
    animation-duration: 200ms;
    animation-fill-mode: forwards;
    animation-name: scrollONE;
}
.scrol2{
    animation-name: scrollTWO;
    animation-timing-function: ease-in-out;
    animation-duration: 200ms;
    animation-fill-mode: forwards;
}
.fixed {
    position: fixed;
}

@keyframes scrollONE {
    from {}

    to {
        transform: translate(-28px, 97px) scale(.3);
    }
}
@keyframes scrollTWO {
    from {
        transform: translate(-28px, 97px) scale(.3);
    }
    to {
        transform: scale(1);
    }
}