.search{
    text-align: center;
    font-size: 1.2rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 1rem;
}
/*FORMULARIO*/
.search__form{
    display: flex;
    width: 100%;
    height: 3rem;
    margin-bottom: 1rem;
    display: flex;
    justify-content: center;
}
.search__filter{
    margin-left: 1rem;
}
.search__filter:hover{
    cursor: pointer;
}
.search__text-field{
    margin-left: 10%;
    font-size: 1.5rem;
    /* margin-right: 1rem; */
    width: 40%;
    min-width: fit-content;
    border: 2px solid #888 ;
    border-radius: 1rem 0 0 1rem ;
    height: 100%;
}
.search__button{
    background-color: #273564;
    width: fit-content;
    cursor: pointer;
    border-radius: 0 20% 20% 0;
    transform-origin: left;
    transition: 100ms;
}
.search__button:hover{
    transform: scale(1.2);
}
.search__filter-list{
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.search__filter {
    font-size: 1.5rem;
    color: #555;
    border-radius: 5px;
    width: 100%;
    height: 3rem;
    margin: .5rem;
}
.search__filter:focus {
    /* outline: none; */
    box-sizing: border-box;
    outline:4px solid rgba(144, 144, 144, 0.388);
    border-radius: 5px;

}
#search__result-marc{
    font-size: 1.5rem;
}