.section{
    min-height: 430px;
    width: 100%; 
    background-image: url('../images/Fondo-body.png');
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: minmax(400px,1fr);
    grid-auto-rows: minmax(400px,1fr);
    row-gap: 2rem;
    column-gap: 2rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
}