.product-register{
    text-align: center;
    font-size: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
}
/*FORMULARIO*/
#product-register__form{
    width: 100%;
} 
.product-register__list{
    list-style: none;   
    width: 100%;
    box-sizing: border-box;
    box-sizing: border-box;
}
.product-register__item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 2rem 0 2rem 0;
}
.product-register__item:last-child{
    /* display: flex; */
    justify-content: flex-end;
}
.product-register__item:last-child{
    display: flex;
    justify-content: flex-end;
}

.product-register__field{
    width: 40%;
    min-width: fit-content;
    position: relative;
    right: 10%;
    font-size: 1.5rem;
    height: 3rem;
    border: 2px solid #888 ;
    border-radius: 10px;
}
#product-register__sex,#product-register__estado{
    font-size: 1.5rem;
    height: 3.3rem;
}
.product-register__submit{
    position: relative;
    right: 10%;

}

