.operator-register{
    text-align: center;
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 95%;
    grid-column: 1/2;
    grid-row: 1/2;
}
.operator-register__form{
    width: 100%;
}
.operator-register__form-list{
    list-style: none;
    width: 100%;
    box-sizing: border-box;
}
.operator-register__item{
    display: flex;
    justify-content: space-between;
    margin: 2rem 0 2rem 0;
    
}
.operator-register__item:last-child{
    display: flex;
    justify-content: flex-end;
    position: relative;
    right: 10%;
}
.operator-register__field{
    width: 40%;
    min-width: fit-content;
    position: relative;
    right: 10%;
    height: 3rem;
    border: 2px solid #888 ;
    border-radius: 10px;
}
.operator-register__label{
    position: relative;
    left: 10%;
    font-weight: 600;
    font-size: 1.5rem;
}
.operator-register__data{
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.219);
    border-radius: .5rem;
    background-color: #fff;
    height: 30%;
    width: 80%;
    margin-bottom: 1rem;
    color: #666;
}