.login{
    text-align: center;
    font-size: 1.2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
/*FORMULARIO*/
.login__form{
    width: 100%;
    font-size: inherit;
}
.login__form-list{
    list-style: none;
    padding: 0;
    box-sizing: border-box;
}
.login__item{
    display: flex;
    justify-content: space-between;
    margin: 2rem 0 2rem 0;
}
.login__item:last-child{
    display: flex;
    justify-content: flex-end;
}
.login__label{
    position: relative;
    left: 10%;
    font-weight: 600;
    font-size: 1.5rem;
}
.login__text-field-name, .login__text-field-password{
    width: 40%;
    min-width: fit-content;
    position: relative;
    right: 10%;
    height: 3rem;
    border: 2px solid rgb(124, 124, 124) ;
    border-radius: 10px;
    font-size: 1.5rem;
}
.login__submit{
    position: relative;
    right: 10%;
}
.login__logo{
    height: auto;
    width: 15rem;
    filter: drop-shadow(0 0 3px rgba(0, 0, 0, 0.384));
    margin: 1rem;
}
