.info-data{
    font-size: 1.6rem;
}
.info-data__back{
    position: relative;
    top: 10px;
    left: 0;
}
.info-data__print{
    justify-self: end;
}
/*HOJA-----------------------------------------------------------------*/
.Hoja{
    margin: 3rem 0 2rem 0;
    display: flex;
    background-color: #fff;
}
.hoja__list{
    width: 70%;
    list-style-type: none;
}
.hoja__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    line-height: 0;
    width: 100%;
    padding: .3rem;
    /* background-color: red; */
}
.hoja__item:nth-child(2n + 1){
    background-color: #f3f6f2;
}
.hoja__item:nth-child(2n + 2){
    background-color: #f0e9f2;
}