.product-modifer{
    text-align: center;
    font-size: 1.3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}
#product-modifer__form{
    width: 100%;
}
.product-modifer__form-list{
    list-style: none;   
    width: 100%;
    box-sizing: border-box;
}
.product-modifer__item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 2rem 0 2rem 0;
}
.product-modifer__item:last-child{
    display: flex;
    justify-content: flex-end;
}
.product-modifer__field{
    width: 40%;
    min-width: fit-content;
    position: relative;
    right: 10%;
    height: 3rem;
    border: 2px solid #888 ;
    border-radius: 10px;
}
#product-modifer__sexo,
#product-modifer__estado{
    font-size: 1.5rem;
}
.product-modifer__submit{
    /* border-radius: .5rem;    */
    position: relative;
    right: 10%;
}
