.operator-modifer{
    grid-column: 1/3;
    grid-row: 2/3;
    /* width: 98%; */
    margin-bottom: 1rem;
    display: flex;
}
.operator-modifer__result{
    width: 95%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
/*FORMULARIO*/
.operator-modifer__content{
    width: 95%;
    text-align: center;
}
.operator-modifer__form{
    width: 100%;
}
.operator-modifer__form-list{
    list-style: none;   
    width: 100%;
    box-sizing: border-box;
}
.operator-modifer__item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 2rem 0 2rem 0;
}
.operator-modifer__item:last-child{
    display: flex;
    justify-content: flex-end;
    position: relative;
    right: 10%;
    
}
.operator-modifer__field{
    width: 40%;
    min-width: fit-content;
    position: relative;
    right: 10%;
    height: 3rem;
    border: 2px solid #888 ;
    border-radius: 1rem;
}
.operator-modifer__label{
    font-size: 1.5rem;
    font-weight: 600;
    position: relative;
    left: 10%;
}