:root{
  --colorAzulBase: #273564;
}
html{
  font-size: 10px;
}
body {
  margin:0;
  width: 100%;
}

b{
  color: #44f;
  color: var(--colorAzulBase);
}
.boton{
  padding: 1rem 2rem 1rem 2rem;
    font-size: 1.6rem;
    border: none;
    background-color: rgb(95, 221, 27);
    border-radius: 10px;
    font-weight: 600;
    color: #fff;
}
.boton:hover{
  background-color: #adf5cd;
  color: rgb(51, 120, 29);
  cursor: pointer;
}

.back-default{
  background-color: rgba(255, 255, 255, 0.5);
  /* font-size: 2rem; */
  border-radius: 1rem;
  box-shadow: 0px 0px 5px 3px rgba(0,0,0,.3);
  padding: 1rem;
}
/*ICONO de consulta*/
.search-icon{
  min-height: 1rem;
  max-height: 1rem;
  width: 1rem;
  margin-bottom: .5rem;
  border-radius: 50%;
  border: 4px solid #fff;
}
.search-icon::after{
  content: "";
  display: block;
  background-color: #fff;
  position: relative;
  height: 4px;
  width: 80%;
  top: 1.2rem;
  left: .8rem;
  transform: rotate(54deg);
}
